.wrapper {
    background-color: white;
    top: 0px;
    left: 0px;
    right: 0px;
    box-shadow: 0px 0px 5px rgb(77, 75, 75);
    z-index: 2;
}

.container {
    min-height: 90px;
}
.logo {
    margin-left: 10px;
    line-height: 75px;
}
.logo-div {
    font-family: 'Amatic SC';
    position: relative;
    max-width: 180px;
    min-height: 60px;
    cursor: pointer;
    h1 {
        color: #2f333a;
        width: 100%;
        font-size: 4rem;
        font-weight: 700;
        letter-spacing: 2px;
    }
    p {
        line-height: normal !important;
        font-size: 2rem;
        font-weight: 700;
        position: absolute;
        bottom: 0;
        right: 5px;
    }
}
.navigate {
    display: flex;
    align-items: center;
}
.navigate-item {
    font-family: 'Ubuntu';
    font-size: 2rem;
    letter-spacing: 1px;
    line-height: 75px;
    text-align: center;
    font-weight: 500;
    color: #2f333a;

    cursor: pointer;
    &:hover {
        color: orange;
        transition: all 0.2s linear;
    }
}
//  Responsive library
.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
}

.row.no-gutters {
    margin-left: 0;
    margin-right: 0;
}

.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}

.c-0 {
    display: none;
}

.c-1 {
    flex: 0 0 8.33333%;
    max-width: 8.33333%;
}

.c-2 {
    flex: 0 0 16.66667%;
    max-width: 16.66667%;
}

.c-3 {
    flex: 0 0 25%;
