// PC- 600px height
.wrapper {
    position: relative;
    width: 100%;
    height: 1050px;
}
.icon {
    position: absolute;
    font-size: 5rem;
    top: 50%;
    transform: translateY(-50%);
    color: #eeeeee;
    z-index: 3;
    cursor: pointer;
    &:hover {
        transition: all 0.2s linear;
        color: orange;
    }
}
.left {
    left: 5px;
}
.right {
    right: 5px;
}
.background-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(0.5);
}
.pc {
    display: none !important;
}
.all-wrapper {
    position: absolute;
    top: 0px;
    right: 0px;
    left: 0px;
    bottom: 0px;
    animation: appear 1s linear;
}
@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.all-wrapper-2 {
    position: relative;
}

.header-div {
    position: relative;
    max-width: 160px;
    padding-top: 12px;
    display: flex;
    margin: 0 auto;
    padding-bottom: 10px;
}
.header {
    font-family: 'Ubuntu';
    color: #eeeeee;
    margin-top: 10px;
    font-size: 4rem;
}
.divide {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 5px;
    border-radius: 5px;
    background-color: orange;
}
.container {
    padding: 50px 0px;
}
.img-div {
    margin-top: 10px;
    height: 400px;
    width: 100%;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.title-div {
    position: relative;
}
.title {
    padding-bottom: 10px;
    font-size: 3.5rem;
    color: #eeeeee;

    font-weight: 700;
    font-family: 'Ubuntu';
}
.divide-2 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 70px;
    height: 5px;
    border-radius: 5px;
    background-color: orange;
}
.middle-div {
    min-height: 263px;
}
.description {
    margin-top: 10px;
    padding-bottom: 10px;
    text-align: justify;
    color: #ffffff;

    font-weight: 400;
    font-size: 2rem;
}
.tech-list {
    padding-left: 20px;
    padding-bottom: 10px;
    margin-top: 10px;
    list-style: none;
}
.list-item {
    color: #ffffff;

    font-size: 1.6rem;
    font-weight: 200;
}
.list-item + .list-item {
    margin-top: 5px;
}
.btn-div {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}
.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.4rem;
    font-weight: 700;
    width: 100%;
    height: 28px;
    background-color: white;
    border-style: solid;
    border-width: 2px;
    box-shadow: rgba(0, 0, 0, 0.2) 15px 28px 25px -18px;
    cursor: pointer;
    &:hover {
        transition: all 0.2s linear;
        color: white;
        background-color: orange;
    }
}
.btn + .btn {
    margin-top: 8px;
}
//  Responsive library
.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
}

.row.no-gutters {
    margin-left: 0;
    margin-right: 0;
}

.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}

.c-0 {
    display: none;
}

.c-1 {
    flex: 0 0 8.33333%;
    max-width: 8.33333%;
}

