// PC - 600px height
.wrapper {
    position: relative;
    background-color: #eeeeee;
    height: 1180px;
}
.background-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(0.5);
}
.all-wrapper {
    position: absolute;
    display: none !important;
    top: 0px;
    right: 0px;
    left: 0px;
    bottom: 0px;
    animation: appear 1s linear;
}
@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.all-wrapper-mobile {
    position: absolute;
    top: 0px;
    right: 0px;
    left: 0px;
    bottom: 0px;
}
.container {
    padding-bottom: 20px;
    margin-top: 30px;
}
.title-div {
    position: relative;
    max-width: 200px;
    display: flex;
    margin: 0 auto;
}
.title {
    font-size: 4rem;
    margin-top: 10px;
    padding-bottom: 12px;
    color: #eeeeee;
    font-family: 'Ubuntu';
}
.devide {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 5px;
    border-radius: 5px;
    background-color: orange;
}

.info {
}
.header-div {
    position: relative;
}
.header {
    padding-bottom: 10px;
    font-family: 'Ubuntu';
    font-size: 3.5rem;
    color: #eeeeee;
}
.divide-2 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 70px;
    height: 5px;
    border-radius: 5px;
    background-color: orange;
}
.list {
    padding-top: 10px;
    font-family: 'Roboto';
}
.list-item {
    color: #fff;
    display: flex;
    align-self: center;
    justify-self: center;
    font-size: 1.8rem;
    padding-bottom: 5px;
}
.list-item + .list-item {
    margin-top: 10px;
}
.education {
    padding-top: 15px;
}
.detail {
    padding-top: 15px;
}
.time {
    font-size: 1.6rem;
    padding-bottom: 10px;
    font-weight: 300;
    color: #eeeeee;
}
.major {
    font-size: 1.8rem;
    color: #fff;
}
.info-paragraph {
    padding-top: 5px;
    text-align: justify;
    color: #fff;
    font-weight: 300;
    margin-top: 10px;
    font-size: 2rem;
    line-height: 1.5;
}
//  Responsive library
.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
}

.row.no-gutters {
    margin-left: 0;
    margin-right: 0;
}

.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}

.c-0 {
    display: none;
}

.c-1 {
    flex: 0 0 8.33333%;
    max-width: 8.33333%;
}

.c-2 {
    flex: 0 0 16.66667%;
    max-width: 16.66667%;
}

.c-3 {
    flex: 0 0 25%;
    max-width: 25%;
}

.c-4 {
    flex: 0 0 33.33333%;
    max-width: 33.33333%;
}

.c-5 {
    flex: 0 0 41.66667%;
    max-width: 41.66667%;
}

.c-6 {
    flex: 0 0 50%;
    max-width: 50%;
}

.c-7 {
    flex: 0 0 58.33333%;
    max-width: 58.33333%;
}

.c-8 {
    flex: 0 0 66.66667%;
    max-width: 66.66667%;
}

.c-9 {
    flex: 0 0 75%;
    max-width: 75%;
}

.c-10 {
    flex: 0 0 83.33333%;
    max-width: 83.33333%;
}

.c-11 {
    flex: 0 0 91.66667%;
    max-width: 91.66667%;
}

.c-12 {
    flex: 0 0 100%;
    max-width: 100%;
}

.c-o-1 {
    margin-left: 8.33333%;
}

.c-o-2 {
    margin-left: 16.66667%;
